<template>
    <div id="container" class="mdui-container mdui-p-t-1" style="margin-top: 10px;">
        <div class="mdui-card mt" style="border-radius:15px;">
            <div class="mdui-card-primary">
                <div class="mdui-card-primary-title"></div>
                <div class="mdui-card-primary-subtitle">Web Settings</div>
            </div>
            <div class="mdui-card-content mdui-typo">
                <p class="text_s"></p>
            </div>
        </div>
        <div class="mdui-card mt" style="border-radius:15px;margin-top: 15px;">
            <div class="mdui-card-primary">
                <div class="mdui-card-primary-title">Settings</div>
            </div>
            <div>
                <!--       -->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo" style="padding-bottom: 0"></div>
                </div>
                <div id="background-image">
                    <div class="mdui-textfield">
                        <input class="mdui-textfield-input" type="text" placeholder="" v-model="webTitle"/>
                        <div class="mdui-textfield-helper"></div>
                    </div>
                </div>
                <!--       -->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo"></div>
                </div>
                <div id="background-image">
                    <div class="mdui-textfield">
                        <input class="mdui-textfield-input" type="text" placeholder="URL" v-model="backgroundImage"/>
                        <div class="mdui-textfield-helper">, API</div>
                    </div>
                </div>
                <!--       IP-->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo"></div>
                </div>
                <div id="submission-restrictions">
                    IP&ensp;
                    <input type="number" v-model="ipCount"/>
                    &ensp; [0]
                </div>
                <!--       -->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo"></div>
                </div>
                <div style="margin-right: 32px;margin-left: 32px;">
                    <div class="mdui-textfield">
                        <input class="mdui-textfield-input" type="text" placeholder="Github" v-model="ghProxy"/>
                        <div class="mdui-textfield-helper"></div>
                    </div>
                </div>
                <!--       -->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo" style="padding-bottom: 0"><span style="color: red;"> @ </span></div>
                </div>
                <div id="textfield">
                    <div class="mdui-textfield">
                        <textarea class="mdui-textfield-input" rows="5" v-model="blacklist"></textarea>
                        <div class="mdui-textfield-helper"><span style="color: red;"> @ </span></div>
                    </div>
                </div>
                <!--            -->
                <div class="mdui-card-primary" style="padding-top: 0; padding-bottom: 0">
                    <div class="mdui-card-content mdui-text-color-indigo"></div>
                </div>
                <div id="notice">
                    <div style="border: 1px solid #ccc">
                        <Toolbar
                            style="border-bottom: 1px solid #ccc"
                            :editor="editorRef"
                            :defaultConfig="toolbarConfig"
                            :mode="mode"
                        />
                        <Editor
                            style="height: 310px; overflow-y: hidden;"
                            v-model="valueHtml"
                            :defaultConfig="editorConfig"
                            :mode="mode"
                            @onCreated="handleCreated"
                        />
                    </div>
                </div>
                <!--                -->
                <div class="mdui-float-right" style="margin-right: 15px; margin-top: 15px; margin-bottom: 15px">
                    <button @click="SendSettings" class="mdui-btn mdui-btn-dense mdui-btn-raised btn mdui-p-x-1 mdui-color-green-700 mdui-text-color-white">
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css' //  css

// import { onBeforeUnmount, ref, shallowRef, onMounted } from 'vue'
import { onBeforeUnmount, shallowRef} from 'vue'
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import axios from "axios";
import mdui from "mdui";
export default {
    name: "AdminSettingCom",
    components: { Editor, Toolbar },
    data() {
        return {
            Settings: [],
            valueHtml: "",
            blacklist: "",
            backgroundImage: "",
            ipCount: 0,
            ghProxy: "",
            webTitle: ""
        }
    },
    setup() {
        //  shallowRef
        const editorRef = shallowRef()
        const toolbarConfig = {}
        const editorConfig = { placeholder: '...' }

        // 
        onBeforeUnmount(() => {
            const editor = editorRef.value
            if (editor == null) return
            editor.destroy()
        })

        const handleCreated = (editor) => {
            editorRef.value = editor //  editor 
        }

        return {
            editorRef,
            mode: 'simple',
            toolbarConfig,
            editorConfig,
            handleCreated
        };
    },
    methods: {
        // 
        SendSettings(){
            this.SendSettings = [
                {key: "notice", value: this.valueHtml},
                {key: "blacklist", value: this.blacklist},
                {key: "backgroundImage", value: this.backgroundImage},
                {key: "ipCount", value: this.ipCount.toString()},
                {key: "ghProxy", value: this.ghProxy},
                {key: "webTitle", value: this.webTitle}
            ]
            axios.put("/v2/api/set/settings", this.SendSettings).then((res) => {
                // 
                switch (res.data !== "") {
                    case res.data.code === 2000:
                        mdui.snackbar({
                            message: '',
                            position: 'right-top',
                        });
                        setTimeout(() => {
                            location.reload()
                        }, 1000)
                        break
                }
            }).catch((error) => {
                // 
                mdui.snackbar({
                    message: error,
                    position: 'right-top',
                });
            })
        },
        // 
        GetSettings(){
            // 
            axios.get("/v2/api/set/settings").then((res) => {
                // 
                switch (res.data !== "") {
                    case res.data.code === 2000:
                        if (res.data.data[0] !== undefined){
                            this.valueHtml = res.data.data[0].value
                        }
                        if (res.data.data[1] !== undefined){
                            this.blacklist = res.data.data[1].value
                        }
                        if (res.data.data[2] !== undefined){
                            this.backgroundImage = res.data.data[2].value
                        }
                        if (res.data.data[3] !== undefined){
                            this.ipCount = parseInt(res.data.data[3].value)
                        }
                        if (res.data.data[4] !== undefined){
                            this.ghProxy = res.data.data[4].value
                        }
                        if (res.data.data[4] !== undefined){
                            this.webTitle = res.data.data[5].value
                        }
                }
            }).catch((error) => {
                // 
                console.log(error)
                mdui.snackbar({
                    message: ",",
                    position: 'right-top',
                });
            })
        }
    },
    mounted() {
        this.GetSettings()
    }
}
</script>

<style scoped>
#textfield{
    margin-right: 32px;
    margin-left: 32px;
}
#notice{
    margin-right: 32px;
    margin-left: 32px;
}
#background-image{
    margin-right: 32px;
    margin-left: 32px;
}
#submission-restrictions {
    margin-right: 32px;
    margin-left: 32px;
}
.mdui-card-content{
    line-height: 10px;
}
</style>
